<template>
  <div>
    <base-panel>
      <template slot="panel-head">
        <router-link to="/">主页</router-link>
        /
        <span>发布话题</span>
      </template>
      <template slot="panel-content">
        <el-form :model="ison" :rules="rules" ref="myFrom">
          <el-form-item label="请选择板块：" prop="tab">
            <el-select v-model="ison.tab" placeholder="请选择" clearable>
              <el-option label="分享" value="share">分享</el-option>
              <el-option label="招聘" value="job">招聘</el-option>
              <el-option label="问答" value="ask">问答</el-option>

              <el-option label="客户端测试" value="dev">客户端测试</el-option>
            </el-select>
          </el-form-item>
          <!-- <el-form-item label="请选择板块：" prop="tab">
            <el-select v-model="ison.tab" placeholder="请选择" clearable>
              <el-option label="分享" value="share"></el-option>
              <el-option label="招聘" value="job"></el-option>
              <el-option label="问答" value="ask"></el-option>
              <el-option label="客户端测试" value="dev"></el-option>
            </el-select>
          </el-form-item> -->

          <el-form-item label="表题" prop="title">
            <el-input placeholder="标题字数10字以上" v-model="ison.title" />
          </el-form-item>
          <el-form-item label="账号" prop="content">
            <el-input
              type="textarea"
              placeholder="请输入内容"
              v-model="ison.content"
              maxlength="30"
              show-word-limit
            >
            </el-input>
          </el-form-item>
        </el-form>
        <el-button @click="btn">提交</el-button>
      </template>
    </base-panel>
  </div>
</template>

<script>
import basePanel from "../components/layout/basePanel.vue";
import { create } from "../plugins/api";
export default {
  components: { basePanel },
  data() {
    return {
      ison: {
        tab: "",
        title: "",
        content: "",
      },
      rules: {
        title: [
          { required: true, message: "不可为空", trigger: "blur" },
          {
            min: 10,
            max: 30,
            message: "长度在 10 到 30个字符",
            trigger: "blur",
          },
        ],
        content: [{ required: true, message: "不可为空", trigger: "blur" }],
        // tab: [{ required: true, message: "请选择模块", trigger: "blur" }],
        tab: [{ required: true, message: "不可", trigger: "change" }],
      },
    };
  },
  methods: {
    btn() {
      this.$refs.myFrom.validate(async (a) => {
        if (a) {
          const { ison } = this;
          const accesstoken = sessionStorage.getItem("accesstoken");
          const res = await create(
            accesstoken,
            ison.title,
            ison.tab,
            ison.content
          );
          this.$router.push(`/topic/${res.topic_id}`);
        }
      });
    },
  },
};
</script>

